<template>
  <div class="container">
    <h1></h1>
    <BaseInput v-model="username"
      tips="2-20"
      :rule="{minlength:2,maxlength:20,match:/^[a-zA-z0-9_]+$/}">
      
    </BaseInput>
    <BaseInput v-model="password"
      type="password"
      tips="6-40"
      :rule="{minlength:6,maxlength:40}">
      
    </BaseInput>
    <BaseInput v-model="email"
      tips=""
      :rule="{match:/^[a-zA-z0-9_-]+@[a-zA-z0-9_-]+(\.[a-zA-z0-9_-]+)+$/}">
      
    </BaseInput>
    <div class="role-wrap">
      <span>
        <input type="radio"
          name="role"
          id="worker"
          value="worker"
          v-model="role">
        <label for="worker">worker</label>
      </span>
      <span>
        <input type="radio"
          name="role"
          id="requester"
          value="requester"
          v-model="role">
        <label for="requester">requester</label>
      </span>
    </div>
    <button @click='signup'></button>
  </div>
</template>

<script>
import BaseInput from "@/components/BaseInput";
import { signUp } from "@/api/base";
export default {
  name: "Signup",
  components: {
    BaseInput
  },
  data: function() {
    return {
      username: "",
      password: "",
      email: "",
      role: "worker",
      valid: true
    };
  },
  methods: {
    signup: function() {
      this.validate();
      if (this.valid) {
        signUp(this.username, this.password, this.email, this.role).then(
          response => {
            this.$router.push("/login");
          }
        );
      } else {
        alert("");
      }
    },
    validate: function() {
      var inputs = this.$children;
      var index = 0;
      this.valid = true;
      while (index < inputs.length) {
        this.valid = inputs[index].validate() && this.valid;
        index++;
      }
    }
  }
};
</script>

<style scoped>
h1,
.base-input,
.role-wrap,
button {
  display: block;
  max-width: 350px;
}
.role-wrap {
  text-align: center;
}
.role-wrap > * {
  display: inline-block;
}
</style>



